interface ExtendedAuthenticationExtensionsClientOutputs extends AuthenticationExtensionsClientOutputs {
    largeBlob?: { blob: ArrayBuffer };
}

//     ( )
export function generateSalt() {
    return crypto.getRandomValues(new Uint8Array(16));
}

//    IV
export function generateIV() {
    return crypto.getRandomValues(new Uint8Array(12));
}

interface IError {
    message: string;
    stack: string;
}

//       Web Crypto API
export async function encrypt(secret: string, salt: Uint8Array, iv: Uint8Array) {
    const encoder = new TextEncoder();
    const data = encoder.encode(secret); //  -   

    //    
    const key = await crypto.subtle.importKey(
        "raw",
        salt,
        {name: "PBKDF2"},
        false,
        ["deriveKey"]
    );

    //  PBKDF2     
    const cryptoKey = await crypto.subtle.deriveKey(
        {
            name: "PBKDF2",
            salt: salt,
            iterations: 100000,
            hash: "SHA-256",
        },
        key,
        {name: "AES-GCM", length: 256},
        false,
        ["encrypt", "decrypt"]
    );

    //  
    const encryptedData = await crypto.subtle.encrypt(
        {name: "AES-GCM", iv: iv}, //   IV
        cryptoKey,
        data
    );

    return encryptedData;
}

//    
export async function decrypt(encryptedSecret: ArrayBuffer, salt: Uint8Array, iv: Uint8Array) {
    const key = await crypto.subtle.importKey(
        "raw",
        salt,
        {name: "PBKDF2"},
        false,
        ["deriveKey"]
    );

    const cryptoKey = await crypto.subtle.deriveKey(
        {
            name: "PBKDF2",
            salt: salt,
            iterations: 100000,
            hash: "SHA-256",
        },
        key,
        {name: "AES-GCM", length: 256},
        false,
        ["encrypt", "decrypt"]
    );

    //  
    const decryptedData = await crypto.subtle.decrypt(
        {name: "AES-GCM", iv: iv}, // IV    ,    
        cryptoKey,
        encryptedSecret
    );

    const decoder = new TextDecoder();
    return decoder.decode(decryptedData);
}

